ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: rgba(0, 44, 17,0.7);
    border-radius: 7px;
    box-shadow: 5px 6px 10px #363636;
  }
  
  li {
    float: left;
  }
  
  li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
  }
  
  li a:hover:not(.active) {
    background-color: #111;
  }
  
  .active {
    background-color: #4CAF50;
  }
  .Navbar {
    margin-left: 4%;
    margin-top: 1%;
    margin-right: 4%;
}